import React from 'react';
import ReactDOM from 'react-dom';
import {Link} from 'react-router';
import { Layout, NavDrawer, Panel, Sidebar,AppBar, Checkbox, IconButton } from 'react-toolbox';
import Navigation from 'react-toolbox/lib/navigation';
import profile from '../../static/css/profileview.css';
import contactphoto from '../../static/images/introProfile@3x.png';
import orgchartimg from '../../static/images/introOrgchart@3x.png';

var search1 = {
  margin: "6px"
};

class MyProfile extends React.Component {
  render() {
    return (        
        <div>
        <h1></h1>
         <Header/>
         <ProfileData/>
         <Footer/>
         </div>      
    );
  }
}

class Header extends React.Component {
  render() {
    return (
 <div>
       
        
      </div>
    );
  }
}

class Settings extends React.Component {
  render() {
    return (
 
        <Link to="settings"><button type="submit">Settings</button></Link>
         
      
    );
  }
}

class ProfileData extends React.Component {
  render() {
    return (    
          
        <div id="wrapper" className={profile.wrapper}>
  <div id="content" className={profile.content}> 
       <img src='https://static.asc.aon.net/pfimages/temp_ABB55201ah82492_LThumb.jpg' height="100" width="100" style={search1}/>
      <br/> <span className={profile.title}> Developer </span>
       <br/> <span className={profile.bu}> Aon Service Corporation </span>
        <br/> <span className={profile.location}> Gurgaon </span>
       
      </div>
  <div id="sidebar" className={profile.sidebar}>
      <Contact/>
 <OrgChart/> 
  <div id="cleared" className={profile.cleared}></div>
</div>  
<div id="content2" className={profile.header}>My Summary </div>          
  <div id="content2" className={profile.contents}>I am sharepoint Developer and have worked on Microsoft technology. </div>   
  
  <div id="content2" className={profile.header}>Ask  Me About </div>          
  <div id="content2" className={profile.contents}>Sharepoint, C# , SQL Server</div> 
        
              
     </div>
    );
  }
}

class Contact extends React.Component {
  render() {
    return (    
     <Link to="contact"><img src={contactphoto} height="60" width="60" style={search1}/></Link>  
    );
  }
}

class OrgChart extends React.Component {
  render() {
    return (   
         
       <Link to= "orgchart"> <img src={orgchartimg} height="60" width="60" style={search1}/></Link> 
        
    
    );
  }
}

class Footer extends React.Component {
  render() {
    return (    
       <div></div>        
    
    );
  }
}

export default MyProfile;


