import React from 'react';
import ReactDOM from 'react-dom';
import { List, ListItem, ListSubHeader, ListDivider, ListCheckbox } from 'react-toolbox/lib/list';
import { AppContainer } from 'react-hot-loader';
import '../../static/css/Style.css';
import styles from '../../static/css/Layout.css';
import listprofile from '../../static/images/listProfile@2x.png';


class SearchScreen extends React.Component {
  render() {
    return (        
        <div>
        
         <SearchBar/>
         <RecentBar/>
         <RecentData/>
         <Footer/>
         </div>      
    );
  }
}

class SearchBar extends React.Component {
  render() {
    return (
 
      <div></div>
         
      
    );
  }
}

class RecentBar extends React.Component {
  render() {
    return (      
        <div>
          {/*NO RECENTLY VIEWED PEOPLE 
<img src=''/>*/}

        </div>         
     
    );
  }
}

class RecentData extends React.Component {
  render () {
    return (<div>
      <div className="headerred">
     </div>
  <List ripple >
    <ListSubHeader caption='RECENTLY VIEWED PEOPLE' />
    <ListItem 
      avatar={listprofile}
      caption='Amit Kumar'
      legend="Developer" className={styles.listborder} 
     
    />
    
    <ListDivider className={styles.listborder}  />
    <ListItem
      avatar='https://static.asc.aon.net/pfimages/temp_ABB55201ah82492_LThumb.jpg'
      caption='Gaurav Saxena'
      legend='Manager'
     
    />
     <ListDivider className={styles.listborder}  />
    <ListItem
      avatar='https://static.asc.aon.net/pfimages/temp_ABB55201ah0156166_LThumb.jpg'
      caption='Rahul Verma 2'
      legend='Tester'
     
    /> 
    <ListDivider className={styles.listborder}  />
  </List></div>
);
  }
}

class Footer extends React.Component {
  render() {
    return (    
      <div></div>        
    
    );
  }
}



export default SearchScreen;


